.header {
  max-width: 100%;
  margin: 0 auto;
  padding: 28px 0 32px 27px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  display: flex;
  justify-content: space-between;
}

@media screen and (min-width: 630px ) {
  .header {
    width: 90%;
    max-width: 880px;
    padding: 45px 0 40px;
  }
}
